body {
  font-family: 'PT Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

* {
  box-sizing: border-box;
}

*:focus,
*:active {
  outline: none;
  border: none;
}

::-webkit-scrollbar {
  width: 0px;  /* remove scrollbar space */
  background: transparent;  /* optional: just make scrollbar invisible */
}

/* optional: show position indicator in red */
::-webkit-scrollbar-thumb {
  background: #FF0000;
}

.container {
  width: 1024px;
  margin: 0 auto;
  padding: 30px 0 60px;
}

.btn {
  display: flex;
  padding: 0.75em 2em;
  border: 0.1em solid #FFFFFF;
  margin: 0 auto;
  font-weight: 300;
  color: #FFFFFF;
  text-align: center;
  transition: all 0.2s;
  cursor: pointer;
  border-radius: 5px;
  background: #232323;
}
.btn:hover {
  color:#000000;
  transition: .37s ease;
  background-color:#FFFFFF;
}
/* Customize the label (the container) */
.container-checkbox {
  position: relative;
  padding-left: 35px;
  cursor: pointer;
  font-size: 14px;
  line-height: 25px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Hide the browser's default checkbox */
.container-checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom checkbox */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: #eee;
}

/* On mouse-over, add a grey background color */
.container-checkbox:hover input ~ .checkmark {
  background-color: #ccc;
}

/* When the checkbox is checked, add a blue background */
.container-checkbox input:checked ~ .checkmark {
  background-color: lightblue;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.container-checkbox input:checked ~ .checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
.container-checkbox .checkmark:after {
  left: 9px;
  top: 5px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}
.container-radio {
  display: block;
  position: relative;
  padding-left: 35px;
  cursor: pointer;
  margin-right: 20px;
  margin-bottom: 10px;
  font-size: 14px;
  line-height: 24px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.container-radio .checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: #eee;
  border-radius: 50%;
}
.container-radio .checkmark:after {
  content: "";
  position: absolute;
  display: none;
}
/* Hide the browser's default radio button */
.container-radio input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
/* On mouse-over, add a grey background color */
.container-radio:hover input ~ .checkmark {
  background-color: #ccc;
}
/* When the radio button is checked, add a blue background */
.container-radio input:checked ~ .checkmark {
  background-color: lightblue;
}
/* Show the indicator (dot/circle) when checked */
.container-radio input:checked ~ .checkmark:after {
  display: block;
}
/* Style the indicator (dot/circle) */
.container-radio .checkmark:after {
  top: 9px;
  left: 9px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: white;
}
input[type='text'], input[type='date'], input[type='number'] {
  font-family: 'PT Sans', sans-serif;
  width: 200px;
  padding: 5px 10px;
  border-radius: 10px;
  border: 1px solid #2323;  
}
input:disabled {
  background: lightblue;
  opacity: .3;
}
input[type='number']::-webkit-inner-spin-button,
input[type='number']::-webkit-outer-spin-button {
  opacity: 1;
} 
input[type='date']::-webkit-outer-spin-button, /* Removes arrows */
input[type='date']::-webkit-inner-spin-button, /* Removes arrows */
input[type='date']::-webkit-clear-button { /* Removes blue cross */
  -webkit-appearance: none;
  margin: 0;
}
hr {
  border-radius: 50%;
  margin: 10px 0;
}
@media all and (max-width:30em){
  .btn {
    display: block;
    margin: 0.4em auto;
  }
}
body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

header {
  position: relative;
  text-align: right;
}

a {
  color: #333344;
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}

.hamburger {
  display: none;
}

.icon {
  background-color: #000;
  cursor: pointer;
  display: inline-block;
  height: 4px;
  margin-top: 10px;
  position: relative;
  width: 24px;
}

.icon:before,
.icon:after {
  content: '';
  background-color: #000;
  height: 4px;
  left: 0;
  position: absolute;
  width: 24px;
  transition: all 0.2s ease-in-out;
}

.icon:before {
  top: -8px;
}

.icon:after {
  bottom: -8px;
}

.icon + nav {
  margin-top: 10px;
}

.hamburger ~ nav {
  position: absolute;
  right: -200px;
  opacity: 0;
  transition: all 0.2s;
  width: calc(100% - 20px);
  box-sizing: border-box;
}

.hamburger:checked ~ nav {
  right: 10px;
  opacity: 1;
}

.hamburger:checked + label span {
  background-color: transparent;
}

.hamburger:checked + label span:before {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  top: 0;
}

.hamburger:checked + label span:after {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  bottom: 0;
}

ul {
  background: #eee;
  margin: 0;
  padding: 10px;
}

li {
  list-style: none;
  margin: 0;
  padding: 5px 0;
}

@media screen and (min-width: 768px) {
  .hamburger ~ nav {
    opacity: 1;
    position: relative;
    right: auto;
    width: auto;
  }
  .icon {
    display: none;
  }
  .icon + nav {
    margin-top: 0;
  }
  li {
    display: inline-block;
    padding: 5px 20px;
  }
}
.loader {
  position: relative;
  width: 2.5em;
  height: 2.5em;
  -webkit-transform: rotate(165deg);
          transform: rotate(165deg);
}
.loader:before, .loader:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  width: 0.5em;
  height: 0.5em;
  border-radius: 0.25em;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.loader:before {
  -webkit-animation: before 2s infinite;
          animation: before 2s infinite;
}
.loader:after {
  -webkit-animation: after 2s infinite;
          animation: after 2s infinite;
}

@-webkit-keyframes before {
  0% {
    width: 0.5em;
    box-shadow: 1em -0.5em rgba(225, 20, 98, 0.75), -1em 0.5em rgba(111, 202, 220, 0.75);
  }
  35% {
    width: 2.5em;
    box-shadow: 0 -0.5em rgba(225, 20, 98, 0.75), 0 0.5em rgba(111, 202, 220, 0.75);
  }
  70% {
    width: 0.5em;
    box-shadow: -1em -0.5em rgba(225, 20, 98, 0.75), 1em 0.5em rgba(111, 202, 220, 0.75);
  }
  100% {
    box-shadow: 1em -0.5em rgba(225, 20, 98, 0.75), -1em 0.5em rgba(111, 202, 220, 0.75);
  }
}

@keyframes before {
  0% {
    width: 0.5em;
    box-shadow: 1em -0.5em rgba(225, 20, 98, 0.75), -1em 0.5em rgba(111, 202, 220, 0.75);
  }
  35% {
    width: 2.5em;
    box-shadow: 0 -0.5em rgba(225, 20, 98, 0.75), 0 0.5em rgba(111, 202, 220, 0.75);
  }
  70% {
    width: 0.5em;
    box-shadow: -1em -0.5em rgba(225, 20, 98, 0.75), 1em 0.5em rgba(111, 202, 220, 0.75);
  }
  100% {
    box-shadow: 1em -0.5em rgba(225, 20, 98, 0.75), -1em 0.5em rgba(111, 202, 220, 0.75);
  }
}
@-webkit-keyframes after {
  0% {
    height: 0.5em;
    box-shadow: 0.5em 1em rgba(61, 184, 143, 0.75), -0.5em -1em rgba(233, 169, 32, 0.75);
  }
  35% {
    height: 2.5em;
    box-shadow: 0.5em 0 rgba(61, 184, 143, 0.75), -0.5em 0 rgba(233, 169, 32, 0.75);
  }
  70% {
    height: 0.5em;
    box-shadow: 0.5em -1em rgba(61, 184, 143, 0.75), -0.5em 1em rgba(233, 169, 32, 0.75);
  }
  100% {
    box-shadow: 0.5em 1em rgba(61, 184, 143, 0.75), -0.5em -1em rgba(233, 169, 32, 0.75);
  }
}
@keyframes after {
  0% {
    height: 0.5em;
    box-shadow: 0.5em 1em rgba(61, 184, 143, 0.75), -0.5em -1em rgba(233, 169, 32, 0.75);
  }
  35% {
    height: 2.5em;
    box-shadow: 0.5em 0 rgba(61, 184, 143, 0.75), -0.5em 0 rgba(233, 169, 32, 0.75);
  }
  70% {
    height: 0.5em;
    box-shadow: 0.5em -1em rgba(61, 184, 143, 0.75), -0.5em 1em rgba(233, 169, 32, 0.75);
  }
  100% {
    box-shadow: 0.5em 1em rgba(61, 184, 143, 0.75), -0.5em -1em rgba(233, 169, 32, 0.75);
  }
}
/**
 * Attempt to center the whole thing!
 */
html,
body {
  height: 100%;
}

.loader {
  position: absolute;
  top: calc(50% - 1.25em);
  left: calc(50% - 1.25em);
}

